import React, { lazy, Suspense, useState } from "react";

import { useSelector } from "react-redux";

import ReportComponent from "./ReportComponent";

const PrintReportModal = lazy(() =>
  import("../../Component/ReportModal/PrintReportModal")
);
const PrintPDFReportModal = lazy(() =>
  import("../../Component/ReportModal/PrintPDFReportModal")
);
const PrintExcelReportModal = lazy(() =>
  import("../../Component/ReportModal/PrintExcelReportModal")
);

const CreditPrintReport = () => {
  // props

  const prints = useSelector((state) => state.creditPrintReport.prints);
  const [reportStartDate, setReportStartDate] = useState(new Date());
  const [reportEndDate, setReportEndDate] = useState(new Date());
  const [formData, setFormData] = useState(null);
  // purchase order report component state
  const [showPrintReportModal, setShowPrintReportModal] = useState(false);
  const [showPrintPDFReportModal, setShowPrintPDFReportModal] = useState(false);
  const [showPrintExcelReportModal, setShowPrintExcelReportModal] =
    useState(false);

  return (
    <>
      <ReportComponent
        name="creditPrintReport"
        setShowPrintExcelReportModal={setShowPrintExcelReportModal}
        setShowPrintPDFReportModal={setShowPrintPDFReportModal}
        setShowPrintReportModal={setShowPrintReportModal}
        reportStartDate={reportStartDate}
        setReportStartDate={setReportStartDate}
        reportEndDate={reportEndDate}
        setReportEndDate={setReportEndDate}
        setFormData={setFormData}
      />

      {/* purchase order quick summary report */}
      <Suspense fallback={<div>Loading...</div>}>
        {showPrintReportModal && prints?.length > 0 && (
          <PrintReportModal
            showPrintReportModal={showPrintReportModal}
            setShowPrintReportModal={setShowPrintReportModal}
            formData={formData}
            prints={prints}
          />
        )}
      </Suspense>

      <Suspense fallback={<div>Loading...</div>}>
        {showPrintPDFReportModal && prints?.length > 0 && (
          <PrintPDFReportModal
            showPrintPDFReportModal={showPrintPDFReportModal}
            setShowPrintPDFReportModal={setShowPrintPDFReportModal}
            formData={formData}
            prints={prints}
          />
        )}
      </Suspense>
      <Suspense fallback={<div>Loading...</div>}>
        {showPrintExcelReportModal && prints?.length > 0 && (
          <PrintExcelReportModal
            showPrintExcelReportModal={showPrintExcelReportModal}
            setShowPrintExcelReportModal={setShowPrintExcelReportModal}
            formData={formData}
            prints={prints}
          />
        )}
      </Suspense>
    </>
  );
};

export default React.memo(CreditPrintReport);
