import axiosInstance from "../../utils/axios";

//obtaining all data
export const getPrintReportData = ({
  formatedStartDate,
  formatedEndDate,
  district,
  branch,
  user,
  postsPerPage,
}) => {
  return axiosInstance.get(
    `api/v1/core-app/print-credit-report?offset=0&limit=${postsPerPage}&district=${district}&branch=${branch}&created_by=${user}&date_after=${formatedStartDate}&date_before=${formatedEndDate}`
  );
};

export const getPrevious = (previous) => axiosInstance.get(previous);

export const getNext = (next) => axiosInstance.get(next);

export const getParticularPage = (
  number,
  postsPerPage,
  district,
  branch,
  user,
  formatedStartDate,
  formatedEndDate
) =>
  axiosInstance.get(
    `api/v1/core-app/print-credit-report?offset=${
      (number - 1) * postsPerPage
    }&limit=${postsPerPage}&district=${district}&branch=${branch}&created_by=${user}&date_after=${formatedStartDate}&date_before=${formatedEndDate}`
  );
// all data
export const getAllPrintReportData = ({
  formatedStartDate,
  formatedEndDate,
  district,
  branch,
  user,
}) => {
  return axiosInstance.get(
    `api/v1/core-app/print-credit-report?district=${district}&branch=${branch}&created_by=${user}&date_after=${formatedStartDate}&date_before=${formatedEndDate}&limit=0`
  );
};
