import { creditPrintReportConstants } from "./constants";
// actions
export const loadingAction = () => ({
  type: creditPrintReportConstants.LOADING_REPORT,
});

export const getPrintReportSuccess = (data) => ({
  type: creditPrintReportConstants.GET_CREDIT_PRINT_REPORT_SUCCESS,
  payload: data,
});

export const getPrintReportFailAction = (error) => ({
  type: creditPrintReportConstants.GET_CREDIT_PRINT_REPORT_FAIL,
  payload: error,
});
export const getAllPrintReportSuccess = (data) => ({
  type: creditPrintReportConstants.GET_ALL_CREDIT_PRINT_REPORT_SUCCESS,
  payload: data,
});

export const getAllPrintFailAction = (error) => ({
  type: creditPrintReportConstants.GET_ALL_CREDIT_PRINT_REPORT_FAIL,
  payload: error,
});
